.Modules{
    display: flex;
    justify-content: space-between;
    gap: 10px;
    flex-wrap: wrap;
    margin: 10px;

}

.ModulesFV{
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 60%;
}

.Module{


    border-radius: 20px;
    min-height: 200px;
