.Flex_R {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  font-size: var(--size_menu);
}

.Item {
  padding: 20px;
  box-shadow: 2px 2px 6px grey;
  transition: 0.2s;
  border-radius: 5px;
}

.Item:hover {
  transform: scale(1.1);
  transition: 0.5s;
}

.status {
  padding: 3px;
  margin-top: 5px;
}

.status[data-status="Done"] {
  background-color: #00cdac;
  color: white;
}
.status[data-status="On going"] {
  background-color: #fc6238;
  color: white;
}
